.JsComponent-container {
    background-color: #0E2C45;
    box-sizing: border-box;
    width: 50%;
    border: 10px solid #F5F5F7;
    height: 670px;
    overflow: hidden;
    position: relative;
    float: right;
}

.JsComponent-text {
    margin-left: 18%;

}

.JsComponent-text h1{
    text-align: center;
    display: inline-block;
    font-size: 75px;
    font-family: 'Chivo', sans-serif;
    color: white;
    float: left;
}

.JsComponent-text .Yellow-text {
    color: #F8E500;
}

.JsComponent-slider {
    margin-left: 40px;
    width: 2700px;
    clear: both;
    display: flex;
    flex-direction: row;
}

.JsComponent-slider > div {
    margin-left: 40px;
    overflow: hidden;
}

.JsComponent-slider .PrePrev-elem {
    -webkit-transform: translateX(-220%) scale(1.2);
            transform: translateX(-220%) scale(1.2);
    /*display: none;*/
    transition: all 1s;
    position: absolute;
    height: 330px;
    width: 460px;
}

.JsComponent-slider .Prev-elem {
    -webkit-transform: translateX(-110%) scale(1.2);
            transform: translateX(-110%) scale(1.2);
    transition: all 1s;
    position: absolute;
    height: 330px;
    width: 460px;
}

.JsComponent-slider .Activ-elem {
    transition: all 1s;
    position: absolute;
    margin-top: 10px;
    margin-left: 60px;
    height: 330px;
    width: 65%;
}

.JsComponent-slider .Next-elem {
    -webkit-transform: translateX(120%) scale(1.2);
            transform: translateX(120%) scale(1.2);
    transition: all 1s;
    position: absolute;
    height: 330px;
    width: 65%;
}

.JsComponent-slider .NeNext-elem {
    -webkit-transform: translateX(230%) scale(1.2);
            transform: translateX(230%) scale(1.2);
    /*display: none;*/
    transition: all 1s;
    position: absolute;
    height: 330px;
    width: 460px;
}

.JsComponent-slider > div > img {
    /*width: 100%;*/
    height: 100%;
    transition: all 1s;
}

.Next-btn {
    position: absolute;
    right: 10px;
    margin-top: 105px;
}

.Prev-btn {
    position: absolute;
    left: 10px;
    margin-top: 105px;
}

