.photos-container {
    display: grid;
    grid-template-columns: 33.3333333% 33.3333333% 33.3333333%;
    /*flex-wrap: wrap;*/
    /*flex-direction: row;*/
    padding-bottom: 200px;
}

.photos-container .photoRow .photo {
    /*width: 33.3333333%;*/
    max-width: 100%;
    box-sizing: border-box;
    margin: 30px;
    overflow: hidden;
    max-height: 100%;
    border-radius: 4px;
    /*object-fit: cover;*/
}

.photos-container .photoRow .photo:hover {
    transition: 0.7s;
    -webkit-transform: scale(1.1);
    transform: scale(1.1);
    box-shadow: 10px 10px 10px #D7F1FF;

}

.photos-container .photoRow .photo img{
    width: 100%;
    border-radius: 4px;
    transition: 1s;
}

